
.registerEmployeeFullContent {
  width: 100%;
  padding: 30px;
};

.registerEmployeeFullContent .titleCad2 {
  display: flex;
  justify-content: flex-start;
  margin-top: 30px !important; 
}

.titleLineExp2 {
  border-top: 3px solid #4dcdbc;
  border-radius: 20px;
  width: 52%;
  margin-left: 0.5%;
  margin-top: -1%;
  margin-bottom: 5%;
}

.divInputLabelError2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.buttonCad2 {
  display: flex;
  align-items: center;
  margin-top: 3%;
  margin-left: 25%;

  background-color: #0ec4ab;
  padding: 8px 70px;
  outline: none;
  border: 1px solid rgb(167, 162, 162);
  border-radius: 43px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.horizontalInput {
  display: flex;
  flex-direction: row;

  width: 100%;

  align-items: center;
}

.horizontalInput + .horizontalInput{
  margin-top: 12px;
}

.MuiTextField-root {
    border: 2px solid;
}

.purchaseFinished{

  background-color: #0EC4AB;
  padding: 8px 70px;
  outline: none;
  border: 1px solid rgb(167, 162, 162);
  border-radius: 43px;

  display: flex;
  align-items: center;
  justify-content: center;
  
  font-size: 35px;
  font-weight: normal;
  margin-top: 24px;

  transition: background-color 0.6s ease;
}

/* Retirando setinhas do typy="number" */
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

@media(max-width: 800px){
  .horizontalInput {
    flex-direction: column;
  }
}