.switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 26px;
    outline: none;
}
.switch input {
    position: absolute;
    top: -99999px;
    left: -99999px;
}
.slider {
    border: 2px solid #000;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 34px;
}
.slider:before {
    border: 2px solid #000;
    position: absolute;
    content: "";
    height: 19px;
    width: 19px;
    left: 4px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}
input:checked + .slider {
    background-color: #2196f3;
    border: 2px solid #000;
}
input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
    border: 2px solid #000;
}
input:checked + .slider:before {
    border: 2px solid #000;
    transform: translateX(17px);
}

