/* Inicio Cart Contents -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=- */
.all_window {
  position: absolute;
  top: 25px;
  right: 20px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  height: 175px;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  width: 350px;
  box-sizing: content-box;

  border-left: solid 0.5px rgba(0, 0, 0, 0.2);
  border-right: solid 0.5px rgba(0, 0, 0, 0.2);
  border-bottom: solid 0.5px rgba(0, 0, 0, 0.2);

  z-index: 1;
}

.all_window .blueLine {
  width: 100.6%;
  height: 4px;
  background-color: #4dcdbc;
  margin-bottom: 15px;
  margin: -1.5px;
  z-index: 9;
}

.all_window .products {
  min-height: 90px;
  max-height: 175px;
  overflow: auto;
  width: 100%;
  margin: auto;
  padding: 10px 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.all_window .subtotal_div {
  display: flex;
  justify-content: space-between;
  width: 90%;
  color: black;
  margin: auto;
}

.all_window .subtotal_div h5 {
  color: black;
}

.all_window .stylized_button {
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: black;
  margin-bottom: 10px;
  background-color: #4dcdbc;
  padding: 5px 20px;
  border-radius: 15px;
  border: 0;
  display: flex;
  align-items: center;
}

