.buttonEmployee {
    width: 80%;
    height: 50px;
    background-color: #0EC4AB;
    margin-top: 10px;
    margin-left: 25vh;
    border-radius: 5vh;
    min-width: 100px;
    float: right;
    margin-bottom: 40px;
}

.topEmployee{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.searchEmployee{
    display: flex;
    justify-content: center;
    margin-top: 3vh;
}

#searchEmployee{
    border-radius: 5vh;
    border-color: #201F24;
    height: 5vh;
    width: 40vw;
    color: rbga(0,0,0,0.54);
    padding-left: 5%;
}

#searchEmployee:focus{
    border-color: #3696e4;
}

.searchButtonEmployee{
    color: #201F24;
    margin-left: 3vh;
    margin-top: 1vh;
    font-size: 1.5rem;
    cursor: pointer;
}

.searchButtonEmployee:hover{
    color:  #3696e4;
}

.buttonEmployee:hover{
    text-decoration: none;
    background-color: #0EC4ABAA;
}

