.buttonEmployee button {
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  background-color: transparent;
}

.header-table {
  background-color: #ccc;
}

@media (max-width: 800px) {
  #searchEmployee {
    width: 60vw;
  }

  .searchButtonEmployee {
    margin-right: 5vh;
  }

  .topEmployee {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .buttonEmployee {
    margin-right: 7vh;
  }
}

